@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Marck+Script&family=Merienda:wght@700&display=swap);
/*General Section*/

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
}

html {
  background-color: rgb(216, 216, 216);
  z-index: -5;
}

/*Scrollbar Section*/

/* width */
::-webkit-scrollbar {
  width: 1.1rem;
}

/* Track */
::-webkit-scrollbar-track {
  background: #d9a7c7;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fffcdc, #d9a7c7);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  box-shadow: inset 0 0 5px grey;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(63, 63, 63);
  border-radius: 1.875em;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/*Gallary Section*/

.gal-h1 {
  font-size: 4em;
  text-align: center;
  text-rendering: optimizeLegibility;
}

h1.retroshadow {
  color: #2c2c2c;
  letter-spacing: 0.05em;
  text-shadow: 4px 4px 0px #d5d5d5, 7px 7px 0px rgba(0, 0, 0, 0.2);
}

.gallary {
  background: #ffe259;
  background: linear-gradient(to right, #ffa751, #ffe259);
  padding: 2em;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  width: 100vw;
}

.gal-h1 {
  font-size: 3em;
  text-align: center;
  text-shadow: 0px 4px 3px rgba(0, 0, 0, 0.4), 0px 8px 13px rgba(0, 0, 0, 0.1),
    0px 18px 23px rgba(0, 0, 0, 0.1);
}

.gal-img {
  object-fit: cover;
  border-radius: 0px;
}

/*Projects Section*/

.projects {
  background: #f7ff00;
  background: linear-gradient(to right, #db36a4, #f7ff00);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 2em;
  min-height: 100vh;
  width: 100vw;
}

.proj-h1 {
  font-size: 4em;
  text-align: center;
  text-rendering: optimizeLegibility;
}

/*Contact Me Section*/

.container {
  position: relative;
  width: 100vw;
  min-height: 100vh;
  padding: 2rem;
  background-color: #fafafa;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url(../../static/media/3.e36f56bd.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.input::-webkit-input-placeholder {
  color: rgb(230, 230, 230);
}

.input:-ms-input-placeholder {
  color: rgb(230, 230, 230);
}

.input::placeholder {
  color: rgb(230, 230, 230);
}

.form {
  width: 100%;
  max-width: 820px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0);
  border-radius: 60px;
  box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.1);
  z-index: 998;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  -webkit-backdrop-filter: blur(17px);
          backdrop-filter: blur(17px);
}

.contact-form {
  background-color: hsl(223, 100%, 50%);
  position: relative;
}

.contact-form:before {
  content: "";
  position: absolute;
  width: 26px;
  height: 26px;
  background-color: #1abc9c;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  top: 50px;
  left: -13px;
}

form {
  padding: 2.3rem 2.2rem;
  z-index: 10;
  overflow: hidden;
  position: relative;
}

.title {
  color: #fff79a;
  font-weight: 500;
  font-size: 2.2rem;
  line-height: 1;
  margin-bottom: 0.7rem;
  text-shadow: 4px 0px 0px rgba(255, 0, 0, 0.71);
}

.input-container {
  position: relative;
  margin: 1rem 0;
}

.input {
  width: 100%;
  outline: none;
  border: 2px solid #fafafa;
  background: none;
  padding: 0.6rem 1.2rem;
  color: #fff;
  font-weight: 500;
  font-size: 0.95rem;
  letter-spacing: 0.5px;
  border-radius: 25px;
  transition: 0.3s;
}

textarea.input {
  padding: 0.8rem 1.2rem;
  min-height: 150px;
  border-radius: 22px;
  resize: none;
  overflow-y: auto;
}

.input-container label {
  position: absolute;
  top: 50%;
  left: 15px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding: 0 0.4rem;
  color: #fafafa;
  font-size: 0.9rem;
  font-weight: 400;
  pointer-events: none;
  z-index: 1000;
  transition: 0.5s;
}

.input-container.textarea label {
  top: 1rem;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.cont-btn {
  padding: 0.6rem 1.3rem;
  background-color: rgb(63, 63, 63);
  align-items: center;
  border: 2px solid #fafafa;
  font-size: 0.95rem;
  color: #1abc9c;
  line-height: 1;
  border-radius: 25px;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  margin: 0;
  width: 100%;
}

.cont-btn:hover {
  color: rgb(235, 253, 119);
}

.input-container span {
  position: absolute;
  top: 0;
  left: 25px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 0.8rem;
  padding: 0 0.4rem;
  color: transparent;
  pointer-events: none;
  z-index: 500;
}

.input-container span:before,
.input-container span:after {
  content: "";
  position: absolute;
  width: 10%;
  opacity: 0;
  transition: 0.3s;
  height: 5px;
  background-color: #1abc9c;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.input-container span:before {
  left: 50%;
}

.input-container span:after {
  right: 50%;
}

.input-container.focus label {
  top: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 25px;
  font-size: 0.8rem;
}

.input-container.focus span:before,
.input-container.focus span:after {
  width: 50%;
  opacity: 1;
}

.contact-info {
  padding: 2.3rem 2.2rem;
  position: relative;
}

.text {
  color: #fff79a;
  margin: 1.5rem 0 2rem 0;
  text-shadow: 2px 0px 0px rgba(0, 0, 0, 0.71);
}

.cont-p {
  color: #fff79a;
}

.information {
  display: flex;
  color: #555;
  margin: 0.7rem 0;
  align-items: center;
  font-size: 0.95rem;
}

.icon {
  width: 28px;
  margin-right: 0.7rem;
}

.social-media {
  padding: 2rem 0 0 0;
}

.social-media p {
  color: #fff79a;
}

.social-icons {
  display: flex;
  margin-top: 0.5rem;
}

.social-icons a {
  width: 35px;
  height: 35px;
  border-radius: 5px;
  background: linear-gradient(45deg, #1abc9c, #149279);
  color: #fff;
  text-align: center;
  line-height: 35px;
  margin-right: 0.5rem;
  transition: 0.3s;
}

.social-icons a:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.contact-info:before {
  position: absolute;
  width: 110px;
  height: 100px;
  border: 22px solid #1abc9c;
  border-radius: 50%;
  bottom: -77px;
  right: 50px;
  opacity: 0.3;
}

.square {
  position: absolute;
  height: 400px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(181%, 11%);
          transform: translate(181%, 11%);
  opacity: 0.2;
}

@media (max-width: 850px) {
  .form {
    grid-template-columns: 1fr;
  }

  .contact-info:before {
    bottom: auto;
    bottom: initial;
    top: -75px;
    right: 65px;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }

  .contact-form:before {
    top: -13px;
    left: auto;
    left: initial;
    right: 70px;
  }

  .square {
    -webkit-transform: translate(140%, 43%);
            transform: translate(140%, 43%);
    height: 350px;
  }

  .big-circle {
    bottom: 75%;
    -webkit-transform: scale(0.9) translate(-40%, 30%);
            transform: scale(0.9) translate(-40%, 30%);
    right: 50%;
  }

  .text {
    margin: 1rem 0 1.5rem 0;
  }

  .social-media {
    padding: 1.5rem 0 0 0;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 1.5rem;
  }

  .contact-info:before {
    display: none;
  }

  .square,
  .big-circle {
    display: none;
  }

  form,
  .contact-info {
    padding: 1.7rem 1.6rem;
  }

  .text,
  .information,
  .social-media p {
    font-size: 0.8rem;
  }

  .title {
    font-size: 1.15rem;
  }

  .social-icons a {
    width: 30px;
    height: 30px;
    line-height: 30px;
  }

  .icon {
    width: 23px;
  }

  .input {
    padding: 0.45rem 1.2rem;
  }

  .cont-btn {
    padding: 0.45rem 1.2rem;
  }
}

